<template>
	

<div class="personalAuthen_page">
	
	<header-top title='' back></header-top>
	<div class="fixed-top-div"></div>



    <div class="content gerenrenzheng">

        <form action="" class="layui-form">

            <div class="tips1">
                
                
                
            </div>
            <div class="form-tit">
                
            </div>

            <div class="form-group">
                <label for="name"></label>
                <input type="text" v-model="user.name" placeholder="">
            </div>
            <div class="form-group">
                <label for="id-card"></label>
                <input type="text" v-model="user.idcardno" id="id-card" placeholder="">
            </div>
            <div class="form-group">
                <label for="phone"></label>
                <input type="text" v-model="user.phone"  id="phone" placeholder="">
            </div>
            <div class="form-group">
                <label for="area">/</label>
                <input type="text" readonly v-model="user.city" @click="showSelectCityBox=true" id="area" placeholder="">
            </div>
            <div class="form-group">
                <label for="address"></label>
                <input type="text" v-model="user.address" id="address" placeholder="">
            </div>
            
            <div class="form-group from-img">
                <div>
                    <p></p>
                </div>
                <div>
                    <p></p>
                </div>
            </div>

            <div>

              <!-- //:clickable=false -->

              <!--  -->
              <drop-zone id="myVueDropzone" ref='uploadImg'  useFontAwesome="false" enctype="multipart/form-data" methods='post' 
                :autoProcessQueue=false
                :useFontAwesome=false

                :maxFileSizeInMB=10
                :uploadMultiple=true
                :maxNumberOfFiles=2
                :language="{dictRemoveFile:'',dictCancelUpload:'',dictFileTooBig:'{{filesize}}Mib{{maxFilesize}}Mib'}"

                :headers="{cache:false,processData:false,contentType:false}"
                acceptedFileTypes="image/*" 
                url="http://192.168.0.123:8080/beta6.3/loadfile" 
                v-bind:preview-template="template"
                v-on:vdropzone-success="showSuccess"
                v-on:vdropzone-success-multiple="showSuccess2"
                v-on:vdropzone-file-added="addImg"
                v-on:vdropzone-removed-file="removeImg"
                v-on:vdropzone-queue-complete="complete"
              >

            <div @click="deleteImg(index)" class="dz-preview dz-image-preview" style="width:50%;height:100%;padding:.1rem;margin:0;" v-for=" (item,index) in imgList" v-if="item">
                <div class="dz-image" style="width:100%;height:100%;display:flex;justify-content:center;align-items:center;">
                    <img style="height:8rem;width:100%" :src="item"/>
                </div>
                <div class="dz-progress">
                  <span class="dz-upload" data-dz-uploadprogress=""></span>
                </div>
                <a class="dz-remove" data-dz-remove="" style="background:#3883FF;opacity:1;"></a>
            </div>

              <div class="dz-default dz-message" v-show=" imgList.length<2 "><span><i class="material-icons"></i><br></span></div>

                  <!-- Optional parameters if any! -->
                  <!-- <input type="hidden" name="token" value="xxx"> -->
              </drop-zone>

            </div>

            <div class="tips2">
                
                .jpg.jpeg
                png.gif10MB
            </div>

            <div class="form-btn">
                <!-- <p v-if="personReal" class="btn btn-button center" v-text="statusText"></p> -->
                <input v-if='user.state==0' type="submit" @click.prevent="uploadPhoto" value="  " class="btn btn-button">
                <p v-else-if='user.state==1' class='btn btn-button'></p>
                <p v-else-if='user.state==2' class='btn btn-button'></p>
                <input v-else type="submit" @click.prevent="uploadPhoto" value="  " class="btn btn-button">

            </div>

        </form>
    </div>

<!-- <transition>
  <router-view></router-view>
</transition> -->

<transition>
  <city v-show='showSelectCityBox' @selectCity='selectCity'></city>
</transition>


<transition>
  <product-type v-show="showSelectProductTypeBox" @selectProductType="selectProductType"></product-type>
</transition>


</div>


</template>

<script>

import city from '@/components/common/city'

import productType from '@/components/common/productType'
// import headerTop from '@/components/header/header'	
import headerTop from '@/components/header/header'

import {getCookie} from '@/config/util'
import dropZone from 'vue2-dropzone'

// dropZone.autoDiscover = false;

// console.log(dropZone)


export default{

	components:{
		headerTop,
    dropZone,
    city,
    productType
	},
	data(){
		return{
      currentCity:null,
      showSelectCityBox:false,
      showSelectProductTypeBox:false,

      user:{


        name:'',
        idcardno:'',
        phone:'',
        city:'',
        photo:[],

        aId1:'',
        aId2:'',
        aId3:'',
        address:''

      },
      newImgList:[],//
      imgList:[],//

		}
	},
  mounted(){

    this.$nextTick(()=>{
      this.user.vip = this.$store.getters.getVip;
      // console.log(dropZone);

      // if(getCookie("personAuthen")==1){
        //
        this.queryUserInfo();
      // }



    })


  },
  methods:{

    //
    checkInput(){

        if(this.user.name == ""){
          layer.msg("")
          return;
        }

        if(this.user.idcardno == ""){
          layer.msg("")
          return;
        }

        if(this.user.phone == ""){
          layer.msg("")
          return;
        }

        if(this.user.city == ""){
          layer.msg("/")
          return;
        }

        if(this.user.address == ""){
          layer.msg("")
          return;
        }

        //imgListuploadimg
        if(this.imgList.length < 2 
          && this.imgList.length < 2 
          && (this.imgList.length + this.$refs.uploadImg.dropzone.files.length <2) ){
          layer.msg("")
          return;
        }

        return true;

    },


    //
    addImg(file){

      if(this.$refs.uploadImg.dropzone.files.length > 2){
        this.$refs.uploadImg.removeFile(file);
        layer.msg("");
      }
    },
    //
    complete(){
      if(this.user.state == 0){//

        let newImgSrc = this.newImgList.join(',');
        this.user.photo = newImgSrc;
      }else{
        this.user.photo = this.newImgList.join(',');

      }


      //
      this.submitForm();


    },
    //
    deleteImg(id){


      this.imgList.splice(id,1);

      return;

      console.log(this.imgList.length);
      let e = event || window.event;
      let target = e.target || e.srcElement;
      while(true){

        if(target.getAttribute("class") && target.getAttribute("class").indexOf("dz-preview") >= 0){

          // target.remove();

          if(this.imgList.length>=2){
            this.imgList.splice(id,1);
          }else{
            this.imgList.splice(0,1);
          }

          console.log(id);
        
          break;
        }
        target = target.parentNode;
      }

    },
    //
    queryUserInfo:function(){
        //http://192.168.0.123:8080/zswh5/querygrsmrz?vip=ZS1703161274
        var vm = this;
        this.$http.post('/querygrsmrz',{vip:this.$store.getters.getVip}).then((res)=>{
            console.log(res.data);

            //
            if(res.data.code == 0 && res.data.response.state){
              let user = res.data.response;
              this.user = user;
              this.user.city = user.a_id3;
              this.imgList = user.photo.substring(0,user.photo.length-1).split(',');

        
            }


        })
        //
    },



    getCount(){
      // console.log(this.$refs.uploadImg.$options);
      // console.log(dropZone);
      console.log(this.$refs.uploadImg);
      console.log(this.$refs.uploadImg.dropzone.files.length);

    },
    selectCity(item){
      //
      if(item.getAttribute("data-level")==3){

        this.user.aId1 = item.getAttribute("data-topid");
        this.user.aId2 = item.getAttribute("data-superid");
        this.user.aId3 = item.getAttribute("data-id");

        //
      }else if(item.getAttribute("data-level")==2 && item.getAttribute("data-id") != "QY201606011002"){

        this.user.aId1 = item.getAttribute("data-superid")
        this.user.aId2 = item.getAttribute("data-id")
        this.user.aId3 = ""

      }else{

        layer.msg("/");

        return;
      }

      this.user.city = item.textContent;
      this.showSelectCityBox = false;
    },
    selectProductType(item){
      this.showSelectProductTypeBox = false;
      console.log(item);
    },

    dropzoneTips(){
      // console.log("dropZone");
    },
    uploadPhoto(){

      if(!this.checkInput()) return;

      //
      if(this.$refs.uploadImg.dropzone.files.length > 0){
        //
        this.$refs.uploadImg.processQueue();

      }else{ //

        this.submitForm();

      }

    },
    //
    submitForm(){

      console.log("============");
      let url;
      if(this.user.state==0){
        url = "/updateSmrzinfo"
        //
        if(this.newImgList.length > 0){
          for(let i = 0;i<this.imgList.length;i++){
            let currentSrc = this.imgList[i];
            this.user.photo += ","+currentSrc.substring(currentSrc.lastIndexOf("/")+1,currentSrc.length);
          }
        }else{ //
          this.user.photo = "";
          for(let i = 0;i<this.imgList.length;i++){
            let currentSrc = this.imgList[i];
            this.user.photo += currentSrc.substring(currentSrc.lastIndexOf("/")+1,currentSrc.length)+",";
          }
        }
      }else{
        url = "/grsmrz"
      }

      this.$http.post(url,this.user).then((res)=>{
        if(res.data.code==0){
          layer.open({
            content:'1',
            btn:'',
            end:function(){
              location.reload();
            }
          })

          //
          this.$store.commit("updateAuthenState",this.$store.getters.getVip);

        }else{

          layer.open({
            content:res.data.message||res.data.response,
            style:"width:80%",
            btn:'',
            time:2
          })


        }
      })
    },

    //
    showSuccess(file,res){
      if(res.code==0){
        console.log(res);
        this.newImgList.push(res.response.substring(0,res.response.length-1));
      }
    },

    //
    removeImg(file){

      for(let i = 0;i<this.user.photo.length;i++){
        let obj = this.user.photo[i];
        if(obj['name']==file.name){
          this.user.photo.splice(i,1)
        }
      }

    },
    //
    showSuccess2(file,res){
      // console.log(file);
      // console.log(res);
    },

    template() {
          return `
                  <div class="dz-preview dz-file-preview" style="width:50%;height:100%;padding:.1rem;margin:0;">
                      <div class="dz-image" style="width:100%;height:100%;display:flex;justify-content:center;align-items:center;">
                          <img data-dz-thumbnail />
                      </div>
                      <div class="dz-progress">
                        <span class="dz-upload" data-dz-uploadprogress></span>
                      </div>

                      <div class="dz-error-message" style="top:inherit;"><span data-dz-errormessage></span></div>

                      <div class="dz-details">
                        <div class="dz-size"><span data-dz-size></span></div>
                        <div class="dz-filename"><span data-dz-name></span></div>
                      </div>
                  </div>
              `;
      }

      }
  }



</script>
<style lang='less' scoped>
@import '../../../assets/css/variable.less';



#myVueDropzone{
  overflow: hidden;

  display: flex;
  justify-content: flex-start;

  /*flex-wrap: wrap;*/

  div{
    flex:1;
  }


}
/*#myVueDropzone{
  .dz-image{
    display: flex;
    justify-content: center;
    align-items: center;
    width:100%;
    height:100%;
  }
}
*/





.personalAuthen_page{

	.fixed();

}


/*//*/

.gerenrenzheng{

  background: #fff;
  .tips1,.tips2{
    background: #EAEAEA;
    color:#f00;
    padding:8/@root-font-size 9/@root-font-size;
    font-size:.6rem;
    line-height:.9rem;
  }

/*  .tips2{
    font-size:.6rem;
    color:#f00;
    padding:8/@root-font-size 9/@root-font-size;
  }*/


  
  .border-bottom{
    border-bottom:1px solid #E4E4E4;
  }

  .form-tit{
    text-align: center;
    line-height:35/@root-font-size;
    .border-bottom;
    font-size:.78rem;
  }

  .form-group{
    .border-bottom;
    padding:10/@root-font-size 12/@root-font-size;

    display: flex;
    justify-content: flex-start;

    label{

      width:35%;
      font-size:.7rem;
      display: flex;
      align-items: center;

    }
    input{
      width:72%;
      font-size:.7rem;
      /*box-sizing:border-box;*/
    }

    

  }

  .from-img{

    padding:5/@root-font-size 12/@root-font-size;

/*  	display: flex;
  	justify-content:space-around; */

    div{
/*		width:49%;*/
  		flex:1;
      padding:.1rem;

    }
    img{
      height:90/@root-font-size;
      width:100%;
    }
    p{
      font-size:.7rem;
      color:#f00;

    }
    p.img{
      position: relative;
      color:#f00;
    }
    input{
      opacity:0;
      width:100%;
      height:100%;
      left:0;
      top:0;
      position: absolute;
    }
  }

  .form-btn{
    padding:8/@root-font-size 9/@root-font-size;

    .btn{
    	font-size:.8rem;
      width:100%;
      line-height:38/@root-font-size;
      background: #389E3A;
      color:#fff;
      border-radius:4px;
      text-align: center;
    }
  }


}




.hide{
  display: none !important;
}

/* vue-dropzone css*/

.dropzone{
  padding:0;

}

.vue-dropzone {


  /*overflow: hidden;*/
  height:8rem;



    font-family: 'Arial', sans-serif;
    letter-spacing: 0.2px;
    color: #777;
    transition: background-color .2s linear;
    &:hover {
        background-color: #F6F6F6;
    }
    i {
        color: #CCC;
    }
    .dz-preview {


    
        .dz-image {
            border-radius: 1;
            &:hover {
                img {
                    transform: none;
                    -webkit-filter: none;
                }
            }
        }
        .dz-details {
            bottom: 0;
            top: 0;
            color: white;
            background-color: rgba(33, 150, 243, 0.8);
            transition: opacity .2s linear;
            text-align: left;
            .dz-filename span, .dz-size span {
                background-color: transparent;
            }
            .dz-filename:not(:hover) span {
                border: none;
            }
            .dz-filename:hover span {
                background-color: transparent;
                border: none;
            }
        }
        .dz-progress .dz-upload {
            background: #cccccc;
        }
        .dz-remove {
            position: absolute;
            z-index: 30;
            color: white;
            margin-left: 15px;
            padding: 10px;
            top: inherit;
            bottom: 15px;
            border: 2px white solid;
            text-decoration: none;
            text-transform: uppercase;
            font-size: 0.8rem;
            font-weight: 800;
            letter-spacing: 1.1px;
            opacity: 0;
        }
        &:hover {
            .dz-remove {
                opacity: 1;
            }
        }
        .dz-success-mark, .dz-error-mark {
            margin-left: auto !important;
            margin-top: auto !important;
            width: 100% !important;
            top: 35% !important;
            left: 0;
            i {
                color: white !important;
                font-size: 5rem !important;
            }
        }
    }
}

</style>


